body{
  background-color: black;
}
button{
  background-color: rgb(10, 10, 216);
  color:white;
  border-radius: 5px;
  border:2px solid white;
  cursor:pointer;
  margin-left: 4px;
  
}
button:hover{
  color: black;
  background-color: gray;
}
/* rgb(13, 180, 141) */
.notesbox{
  background-color:rgb(13, 180, 141)  ;
